/**
 * =============================================================================
 * Twilio Webhook Handler - Incoming SMS Messages (Opt-Out Processing)
 * =============================================================================
 * 
 * Processes incoming SMS replies from clients, primarily for:
 * 1. SMS opt-out requests (STOP, UNSUBSCRIBE, END, QUIT, CANCEL)
 * 2. SMS opt-in requests (START, SUBSCRIBE, YES)
 * 3. Payment confirmations via SMS reply
 * 
 * CRITICAL COMPLIANCE:
 * - UK PECR requires immediate honoring of opt-out requests
 * - GDPR Article 21 requires right to object to processing
 * - Failure to honor opt-outs can result in Â£500k+ fines from ICO
 * 
 * Security: Validates Twilio signatures to prevent spoofing
 * =============================================================================
 */

import { NextResponse } from 'next/server';
import type { NextRequest } from 'next/server';
import twilio from 'twilio';
import { handleSMSOptOut, formatUKPhone } from '@/lib/twilio-sms';
import { db, Timestamp } from '@/lib/firebase';
import { logInfo, logError, logWarn } from '@/utils/logger';

/**
 * Verify Twilio webhook signature
 * SECURITY: Prevents unauthorized parties from spoofing Twilio webhooks
 */
function verifyTwilioSignature(
    request: NextRequest,
    body: Record<string, any>
): boolean {
    const signature = request.headers.get('x-twilio-signature');
    const authToken = process.env.TWILIO_AUTH_TOKEN;
    const webhookUrl = `${process.env.NEXT_PUBLIC_APP_URL}${request.nextUrl.pathname}`;

    if (!signature || !authToken) {
        logWarn('Twilio webhook missing signature or auth token');
        return false;
    }

    try {
        return twilio.validateRequest(authToken, signature, webhookUrl, body);
    } catch (error) {
        logError('Error verifying Twilio signature', error);
        return false;
    }
}

/**
 * Detect opt-out keywords in message
 */
function containsOptOutKeyword(message: string): boolean {
    const keywords = ['STOP', 'STOPALL', 'UNSUBSCRIBE', 'CANCEL', 'END', 'QUIT'];
    const upperMessage = message.toUpperCase().trim();
    return keywords.some(keyword => upperMessage === keyword || upperMessage.includes(keyword));
}

/**
 * Detect opt-in keywords in message
 */
function containsOptInKeyword(message: string): boolean {
    const keywords = ['START', 'YES', 'SUBSCRIBE', 'UNSTOP'];
    const upperMessage = message.toUpperCase().trim();
    return keywords.some(keyword => upperMessage === keyword || upperMessage.includes(keyword));
}

/**
 * Handle SMS opt-in request
 */
async function handleSMSOptIn(phoneNumber: string): Promise<{
    success: boolean;
    usersUpdated: number;
}> {
    try {
        const normalizedPhone = formatUKPhone(phoneNumber) || phoneNumber;

        logInfo('Processing SMS opt-in request', { phoneNumber: normalizedPhone });

        const usersSnapshot = await db
            .collection('users')
            .where('phoneNumber', '==', normalizedPhone)
            .get();

        if (usersSnapshot.empty) {
            logWarn('No users found for opt-in phone number', { phoneNumber: normalizedPhone });
            return { success: true, usersUpdated: 0 };
        }

        const batch = db.batch();
        const now = Timestamp.now();

        usersSnapshot.docs.forEach((doc) => {
            batch.update(doc.ref, {
                'collectionsConsent.smsConsent': true,
                'collectionsConsent.smsOptedOut': false,
                'collectionsConsent.smsOptInDate': now,
                updatedAt: now,
            });
        });

        await batch.commit();

        logInfo('SMS opt-in processed successfully', {
            phoneNumber: normalizedPhone,
            usersUpdated: usersSnapshot.size,
        });

        return { success: true, usersUpdated: usersSnapshot.size };
    } catch (error: any) {
        logError('Failed to process SMS opt-in', error, { phoneNumber });
        return { success: false, usersUpdated: 0 };
    }
}

/**
 * Send TwiML response to user
 */
function sendTwiMLResponse(message: string): NextResponse {
    const twiml = `<?xml version="1.0" encoding="UTF-8"?>
<Response>
  <Message>${message}</Message>
</Response>`;

    return new NextResponse(twiml, {
        status: 200,
        headers: { 'Content-Type': 'application/xml' },
    });
}

/**
 * POST /api/webhooks/twilio/incoming-sms
 * Handles incoming SMS messages (opt-out, opt-in, replies)
 */
export async function POST(request: NextRequest) {
    try {
        // Parse form data (Twilio sends application/x-www-form-urlencoded)
        const formData = await request.formData();
        const body: Record<string, any> = {};
        formData.forEach((value, key) => {
            body[key] = value.toString();
        });

        // Verify webhook signature (SECURITY)
        if (process.env.NODE_ENV === 'production') {
            const isValid = verifyTwilioSignature(request, body);
            if (!isValid) {
                return NextResponse.json(
                    { error: 'Invalid signature' },
                    { status: 401 }
                );
            }
        }

        const {
            From: senderPhone, // Client's phone number
            To: recipientPhone, // Our Twilio number
            Body: messageBody, // SMS content
            MessageSid: messageSid,
        } = body;

        logInfo('Twilio incoming SMS webhook received', {
            from: senderPhone,
            to: recipientPhone,
            messageSid,
            bodyLength: messageBody?.length || 0,
        });

        // =========================================================================
        // HANDLE OPT-OUT (CRITICAL - PECR/GDPR COMPLIANCE)
        // =========================================================================
        if (containsOptOutKeyword(messageBody)) {
            logInfo('SMS opt-out keyword detected', {
                from: senderPhone,
                keyword: messageBody.trim(),
            });

            const result = await handleSMSOptOut(senderPhone);

            if (result.success) {
                // Send confirmation per UK PECR requirements
                return sendTwiMLResponse(
                    'You have been unsubscribed from SMS notifications from Relay. ' +
                    'You will not receive any further messages. ' +
                    'Reply START to opt back in.'
                );
            } else {
                logError('Failed to process opt-out', new Error(result.error || 'Unknown error'), {
                    phoneNumber: senderPhone,
                });
                return sendTwiMLResponse(
                    'We received your opt-out request but encountered an error. ' +
                    'Please contact support@relay.app to complete your opt-out.'
                );
            }
        }

        // =========================================================================
        // HANDLE OPT-IN
        // =========================================================================
        if (containsOptInKeyword(messageBody)) {
            logInfo('SMS opt-in keyword detected', {
                from: senderPhone,
                keyword: messageBody.trim(),
            });

            const result = await handleSMSOptIn(senderPhone);

            if (result.success && result.usersUpdated > 0) {
                return sendTwiMLResponse(
                    'You have been re-subscribed to SMS notifications from Relay. ' +
                    'Reply STOP at any time to opt out.'
                );
            } else {
                return sendTwiMLResponse(
                    'Thank you for your message. To manage your SMS preferences, ' +
                    'please log in to your Relay account at relay.app'
                );
            }
        }

        // =========================================================================
        // HANDLE OTHER REPLIES (Payment confirmation, general inquiry)
        // =========================================================================
        logInfo('SMS reply received (not opt-out/opt-in)', {
            from: senderPhone,
            message: messageBody,
        });

        // Generic response for other messages
        return sendTwiMLResponse(
            'Thank you for your message. For assistance, please visit relay.app or reply STOP to opt out of SMS.'
        );

    } catch (error) {
        logError('Twilio incoming SMS webhook error', error);

        // Return error response in TwiML format
        return sendTwiMLResponse(
            'We encountered an error processing your message. Please try again later.'
        );
    }
}

/**
 * GET /api/webhooks/twilio/incoming-sms
 * Health check for webhook configuration
 */
export async function GET() {
    return NextResponse.json({
        service: 'Twilio Incoming SMS Webhook',
        status: 'healthy',
        timestamp: new Date().toISOString(),
    });
}
